.App {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
  min-height: 100vh;
  padding: 20px;
}

.dashboard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.left,
.right {
  flex: 1;
  margin: 10px;
}

h2 {
  color: #ff9800;
}

label,
select,
input {
  display: block;
  margin: 10px 0;
  background-color: #1e1e1e;
  color: #ffffff;
  border: 1px solid #333333;
  padding: 5px;
}

p {
  font-size: 1.2em;
}

@media (max-width: 768px) {
  .dashboard {
    flex-direction: column;
  }
}
